"use client";

import React, { useState } from "react";
import { Student, Transaction } from "../types";
import { Modal } from "./ui/Modal";
import { Input } from "./ui/Input";
import { Button } from "./ui/Button";
import useSound from "use-sound";

interface StudentDetailsModalProps {
  isOpen: boolean;
  onClose: () => void;
  student: Student | null;
  transactions: Transaction[];
  onAddTransaction: (amount: number, description: string) => void;
  onDeleteStudent: () => void;
  onEditStudent: () => void;
}

export const StudentDetailsModal: React.FC<StudentDetailsModalProps> = ({
  isOpen,
  onClose,
  student,
  transactions,
  onAddTransaction,
  onDeleteStudent,
  onEditStudent,
}) => {
  const [amount, setAmount] = useState("");
  const [description, setDescription] = useState("");
  const [showDeleteConfirm, setShowDeleteConfirm] = useState(false);
  const [playNotification] = useSound("/sound/notification.mp3", {
    volume: 0.4,
  });
  if (!student) return null;

  const handleAddMoney = () => {
    const value = parseFloat(amount);
    if (!isNaN(value) && value !== 0) {
      onAddTransaction(value, description || "–ü–æ–ø–æ–ª–Ω–µ–Ω–∏–µ");
      setAmount("");
      setDescription("");
      playNotification();
    }
  };

  const handleSubtractMoney = () => {
    const value = parseFloat(amount);
    if (!isNaN(value) && value > 0) {
      onAddTransaction(-value, description || "–°–ø–∏—Å–∞–Ω–∏–µ");
      setAmount("");
      setDescription("");
      playNotification();
    }
  };

  const handleDeleteStudent = () => {
    onDeleteStudent();
    setShowDeleteConfirm(false);
    onClose();
  };

  const handleEditStudent = () => {
    onEditStudent();
  };

  const age = Math.floor(
    (new Date().getTime() - new Date(student.birthDate).getTime()) /
      (1000 * 60 * 60 * 24 * 365)
  );

  return (
    <Modal
      isOpen={isOpen}
      onClose={onClose}
      title={
        <div
          className="flex items-center justify-between w-full"
          data-oid="-awr83c"
        >
          <span data-oid="j:.7-yr">–£—á–µ–Ω–∏–∫: {student.name}</span>
          <button
            onClick={(e) => {
              e.stopPropagation();
              e.preventDefault();
              handleEditStudent();
            }}
            className="text-gray-400 hover:text-white transition-colors duration-200 p-1 rounded ml-2"
            title="–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å —É—á–µ–Ω–∏–∫–∞"
            data-oid="2.zu:9m"
          >
            <svg
              className="w-4 h-4"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
              data-oid="ujfh:u1"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M15.232 5.232l3.536 3.536m-2.036-5.036a2.5 2.5 0 113.536 3.536L6.5 21.036H3v-3.572L16.732 3.732z"
                data-oid="n00z6s3"
              />
            </svg>
          </button>
        </div>
      }
      data-oid="qrl5oo7"
    >
      <div className="space-y-6" data-oid="1ae384l">
        {/* Student Info */}
        <div className="bg-gray-700 rounded-lg p-4" data-oid="-.vswrr">
          <div className="space-y-2 mb-2" data-oid="rioacbk">
            <div
              className="flex items-center justify-between"
              data-oid="x5rwq1k"
            >
              <span className="text-gray-300" data-oid="bx_gwxh">
                –í–æ–∑—Ä–∞—Å—Ç: {age} –ª–µ—Ç
              </span>
            </div>
            <div
              className="flex items-center justify-between"
              data-oid="1-q2dj4"
            >
              <span className="text-gray-300" data-oid="k159bc0">
                –î–∞—Ç–∞ —Ä–æ–∂–¥–µ–Ω–∏—è:{" "}
                {new Date(student.birthDate).toLocaleDateString("ru-RU", {
                  year: "numeric",
                  month: "long",
                  day: "numeric",
                })}
              </span>
            </div>
          </div>
          <div className="flex items-center justify-end" data-oid="vdds4cc">
            <span
              className={`text-2xl font-bold ${student.balance >= 0 ? "text-green-400" : "text-red-400"}`}
              data-oid="2xjkmwv"
            >
              {student.balance} –ö
            </span>
          </div>
        </div>

        {/* Add/Subtract Money */}
        <div className="space-y-3" data-oid="adyvp51">
          <h3 className="text-lg font-semibold text-white" data-oid="n6r_dot">
            –ò–∑–º–µ–Ω–∏—Ç—å –±–∞–ª–∞–Ω—Å
          </h3>
          <Input
            label="–°—É–º–º–∞"
            type="number"
            value={amount}
            onChange={setAmount}
            placeholder="–í–≤–µ–¥–∏—Ç–µ —Å—É–º–º—É"
            data-oid="wk47-t."
          />

          <Input
            label="–û–ø–∏—Å–∞–Ω–∏–µ (–Ω–µ–æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ)"
            value={description}
            onChange={setDescription}
            placeholder="–û–ø–∏—Å–∞–Ω–∏–µ –æ–ø–µ—Ä–∞—Ü–∏–∏"
            data-oid="obvas:e"
          />

          <div className="flex space-x-3" data-oid="0fovvce">
            <Button
              onClick={handleAddMoney}
              disabled={!amount || parseFloat(amount) <= 0}
              data-oid="uiuhgvy"
            >
              + –î–æ–±–∞–≤–∏—Ç—å
            </Button>
            <Button
              onClick={handleSubtractMoney}
              disabled={!amount || parseFloat(amount) <= 0}
              variant="secondary"
              data-oid="y89x59b"
            >
              - –°–ø–∏—Å–∞—Ç—å
            </Button>
          </div>
        </div>

        {/* Transaction History */}
        <div className="space-y-3" data-oid="8qjg0w9">
          <h3 className="text-lg font-semibold text-white" data-oid="bkrs-ot">
            –ò—Å—Ç–æ—Ä–∏—è –æ–ø–µ—Ä–∞—Ü–∏–π
          </h3>
          <div
            className="max-h-48 overflow-y-auto space-y-2"
            data-oid="cu26mkk"
          >
            {transactions.length === 0 ? (
              <p className="text-gray-400 text-center py-4" data-oid="ooe76:p">
                –û–ø–µ—Ä–∞—Ü–∏–π –ø–æ–∫–∞ –Ω–µ—Ç
              </p>
            ) : (
              transactions
                .sort(
                  (a, b) =>
                    new Date(b.timestamp).getTime() -
                    new Date(a.timestamp).getTime()
                )
                .map((transaction) => (
                  <div
                    key={transaction.id}
                    className="bg-gray-700 rounded p-3"
                    data-oid="2:6a4ox"
                  >
                    <div
                      className="flex items-center justify-between"
                      data-oid="zk.5ml4"
                    >
                      <span className="text-gray-300" data-oid="6tv2nv7">
                        {transaction.description}
                      </span>
                      <span
                        className={`font-semibold ${transaction.amount >= 0 ? "text-green-400" : "text-red-400"}`}
                        data-oid="i8hyi01"
                      >
                        {transaction.amount >= 0 ? "+" : ""}
                        {transaction.amount} –ö
                      </span>
                    </div>
                    <div
                      className="text-xs text-gray-500 mt-1"
                      data-oid="6frhl3-"
                    >
                      {new Date(transaction.timestamp).toLocaleString()}
                    </div>
                  </div>
                ))
            )}
          </div>
        </div>

        {/* Delete Student */}
        <div className="pt-4 border-t border-gray-700" data-oid="2t-54k:">
          {!showDeleteConfirm ? (
            <Button
              onClick={() => setShowDeleteConfirm(true)}
              variant="danger"
              size="sm"
              data-oid="-vzw73m"
            >
              –£–¥–∞–ª–∏—Ç—å —É—á–µ–Ω–∏–∫–∞
            </Button>
          ) : (
            <div className="space-y-2" data-oid="vf2jzbg">
              <p className="text-yellow-400 text-sm" data-oid="augwnh-">
                –í—ã —É–≤–µ—Ä–µ–Ω—ã? –≠—Ç–æ –¥–µ–π—Å—Ç–≤–∏–µ –Ω–µ–ª—å–∑—è –æ—Ç–º–µ–Ω–∏—Ç—å.
              </p>
              <div className="flex space-x-2" data-oid="c3ba0:7">
                <Button
                  onClick={handleDeleteStudent}
                  variant="danger"
                  size="sm"
                  data-oid="x6wc9lu"
                >
                  –î–∞, —É–¥–∞–ª–∏—Ç—å
                </Button>
                <Button
                  onClick={() => setShowDeleteConfirm(false)}
                  variant="secondary"
                  size="sm"
                  data-oid="sdxv1y3"
                >
                  –û—Ç–º–µ–Ω–∞
                </Button>
              </div>
            </div>
          )}
        </div>
      </div>
    </Modal>
  );
};
