import { useState } from "react"
import { Group } from "@/types";
import { supabase } from "@/lib/supabase";

export const useGroups = () => {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  // –ü–æ–ª—É–∏—á—Ç—å –≥—Ä—É–ø–ø—ã
  const fetchGroups = async (): Promise<Group[] | null> => {
    setLoading(true);
    setError(null);

    try {
      const {data, error} = await supabase
        .from('Groups')
        .select("*")
        .order('created_at', {ascending: false})

      if (error) throw error;

      return (data || []).map(g => ({
        ...g,
        createdAt: new Date(g.created_at),
      }));
    } catch (err: any) {
      setError(err.message);
      console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ –≥—Ä—É–ø–ø:', err);
      return null;
    } finally {
      setLoading(false);
    }
  };

  // –°–æ–∑–¥–∞—Ç—å –≥—Ä—É–ø–ø—É
  const createGroup = async (name: string) : Promise<Group | null> => {
    setLoading(true);
    setError(null);
    try {
      const {data, error} = await supabase
        .from('Groups')
        .insert({name})
        .select()
        .single();

      if (error) throw error;
      return {...data, createdAt: new Date(data.created_at)}
    } catch (err: any) {
      setError(err.message);
      console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ —Å–æ–∑–¥–∞–Ω–∏–∏ –≥—Ä—É–ø–ø—ã:', err);
      return null;
    } finally {
      setLoading(false);
    }
  }


  const updateGroup = async (id:string, name: string) : Promise<boolean> => {
    setLoading(true);
    setError(null);
    try {
      const { error } = await supabase
        .from('Groups')
        .update({ name })
        .eq('id', id);

      if (error) throw error;
      return true;
    } catch (err: any){

      setError(err.message);
      console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ –æ–±–Ω–æ–≤–ª–µ–Ω–∏–∏ –≥—Ä—É–ø–ø—ã:', err);
      return false;

    } finally {
      setLoading(false)
    }

  }

  // –£–¥–∞–ª–∏—Ç—å –≥—Ä—É–ø–ø—É (CASCADE —É–¥–∞–ª–∏—Ç –∏ —É—á–µ–Ω–∏–∫–æ–≤!)
  const deleteGroup = async (id: string): Promise<boolean> => {
    setLoading(true);
    setError(null);
    try {
      const { error } = await supabase
        .from('Groups')
        .delete()
        .eq('id', id);

      if (error) throw error;
      return true;
    } catch (err: any) {
      setError(err.message);
      console.error('–û—à–∏–∫–∞ –ø—Ä–∏ —É–¥–∞–ª–µ–Ω–∏–±–∏ –≥—Ä—É–ø–ø—ã:', err);
      return false;
    } finally {
      setLoading(false);
    }
  };


  return {
    loading,
    error,
    fetchGroups,
    createGroup,
    updateGroup,
    deleteGroup,
  };

}